@media screen and (max-width: 1200px) { .section-job-opening-title h2 { font-size: 4rem; } }

@media screen and (max-width: 980px) { .section-job-opening-title h2 { font-size: 3.2rem; } }

@media screen and (max-width: 768px) { .section-job-opening-title h2 { padding: .5rem 0 0; text-align: center; } }

@media screen and (max-width: 374px) { .section-job-opening-title h2 { font-size: 2.8rem; } }

@media screen and (max-width: 768px) { .section-job-opening-title .et_pb_text_inner { text-align: center; } }

@media screen and (max-width: 425px) { .section-job-opening-title .et_pb_text_inner { font-size: 14px; } }

.section-jobs-list { padding-bottom: 5rem !important; padding-top: 2.5rem !important; }

@media screen and (min-width: 981px) { .section-jobs-list > .et_pb_row .et_pb_column:not(:first-child) { padding-bottom: 0; } }

.section-jobs-list ul { padding-left: 0; }

.section-jobs-list ul a:hover { color: #DE5E06; }

@media screen and (max-width: 425px) { .section-jobs-list .et_pb_row { width: 90%; } }

.section-benefits-summary { padding-bottom: 2.2rem !important; padding-top: 0 !important; }

.section-benefits-summary h2 { color: #323845; font-size: 4.8rem; font-weight: 700; font-family: "Antonio", serif; margin-top: 3.5rem; text-transform: uppercase; }

@media screen and (max-width: 480px) { .section-benefits-summary h2 { text-align: center; } }

.section-benefits-summary .et_pb_row:first-child { margin-bottom: -18px; padding-bottom: 0; }

@media screen and (max-width: 480px) { .section-benefits-summary .et_pb_row:first-child { width: 90%; } }

@media screen and (min-width: 981px) { .section-benefits-summary .et_pb_row:first-child .et_pb_column:not(:last-child) { margin-right: 0; } }

.section-benefits-summary .et_pb_row:first-child .et_pb_column:last-child { font-size: 1.5rem; font-weight: normal; line-height: 1.53; padding: 3.2rem 5rem 3.8rem; transform: translateY(-3.2rem); max-width: 72rem; }

@media screen and (min-width: 981px) { .section-benefits-summary .et_pb_row:first-child .et_pb_column:last-child { float: right; right: -5rem; } }

@media screen and (max-width: 480px) { .section-benefits-summary .et_pb_row:first-child .et_pb_column:last-child { padding: 2rem; } }

@media screen and (max-width: 980px) { .section-benefits-summary .et_pb_row:last-child .et_pb_column { margin-bottom: 0; } }

.section-benefits-summary ul { list-style: none; padding: 0; }

.section-benefits-summary ul li { color: #000; font-size: 1.7rem; font-weight: 300; line-height: 1.58; margin-bottom: .8rem; padding: 0 0 0 3.5rem; position: relative; }

.section-benefits-summary ul li:before { background: url(data:img/png;base64,) no-repeat center; content: ''; height: 21px; left: 0; margin-top: 0; position: absolute; right: auto; top: 3px; width: 20px; }

.section-posts { font-size: 1.7rem; font-weight: 300; line-height: 1.52; padding-top: 0; overflow: hidden; }

.section-posts .post { margin-bottom: 0; padding-bottom: 6rem; padding-top: 6.5rem; }

@media screen and (min-width: 981px) { .section-posts .post { padding-left: 23rem; } }

@media screen and (max-width: 980px) { .section-posts .post { padding-top: 4.5rem; } }

.section-posts .post:nth-child(even):before { background: #f7f8f8; }

.section-posts .post:before { content: ''; position: absolute; top: 0; bottom: 0; width: 300%; left: -100%; z-index: -1; }

.section-posts .entry-title { border-bottom: 1px solid #19aabc; color: #0d1319; font-size: 2.8rem; margin-bottom: 4rem; padding-bottom: 2.5rem; position: relative; }

.section-posts .entry-title:before { background-image: linear-gradient(to right, #ea6950 75px, #f7f8f8 0); bottom: -2px; content: ''; height: 3px; left: 0; position: absolute; width: 9.4rem; }

@media screen and (max-width: 980px) { .section-posts .entry-title { font-size: 2.4rem; } }

@media screen and (max-width: 320px) { .section-posts .entry-title { font-size: 2rem; } }

.section-posts .entry-title a { pointer-events: none; }

.section-posts .post-meta { background: #0596aa; font-size: 1.6rem; height: 3.7rem; left: 0; line-height: 3.7rem; margin-bottom: 3rem; top: 7rem; text-align: center; width: 17rem; }

@media screen and (min-width: 981px) { .section-posts .post-meta { position: absolute; } }

.section-posts .post-meta .published { color: #fff; font-weight: normal; }

.section-posts .post-content { color: #000; }

.section-posts ul.custom-list { list-style: none; margin-top: 2.5rem; padding: 0; }

.section-posts ul.custom-list li { margin-bottom: 2.5rem; }

@media screen and (min-width: 581px) { .section-posts ul.custom-list li { display: flex; } }

.section-posts ul.custom-list li span:first-child { display: block; font-weight: 700; flex-shrink: 0; width: 12.5rem; }
